<template>
  <!-- 写论坛模块 -->
  <div class="ForumItem">
            
    <table>
        <tr>
            <td><label id="lable-title" for="title-input">主题:</label></td>
            <td><input v-model="ForumObj.ForumTitle" id="title-input" type="text"></td>
        </tr>
        <tr>
            <td><label id="lable-content" for="content-input">正文:</label></td>
            <td><textarea v-model="ForumObj.ForumContent" id="content-input"></textarea></td>
        </tr>
    </table>
    <label id="lable-fileImg" for="fileImg-input">上传图片({{upImgCount}}/4):</label>
    <div ref="yulanDiv" id="yulanDiv" style="display: flex;">
        <img @click="deletePreVeiwImg(src.imgUrl,src.imgName)" class="yulan" v-show="upImgPreview.length > 0" v-for="(src,index) in upImgPreview" :src="src.imgUrl" :key="index" >
        <input @change="upImg" ref="fileImgInput" id="fileImg-input" accept="image/*" multiple="multiple" type="file">
    </div>
    <button id="issue" type="submit" @click="subMit">发布</button>
    <button id="abandon">放弃</button>

    <!-- 背景大球 -->
    <div class="backgroundp-qiu"></div>
    <!-- 背景小球 -->
    <div class="backgroundp-xiaoqiu"></div>
</div>
</template>

<script>
    import {nanoid} from 'nanoid'
    export default {
        name:'WriteForum',
        data(){
            return {
                upImgCount:0,
                upImgPreview:[],
                ForumObj:{
                    UserName:localStorage.getItem('UserName'),
                    ForumTitle:'',
                    ForumContent:'',
                    SeeView:0,
                    Thumbs:0,
                    CriticismCount:0,
                    upImg:[],
                }
            }
        },
        methods:{
            // 添加上传图片
            upImg(){
                let ImgLength = this.$refs.fileImgInput.files
                if(this.upImgCount >= 4 || ImgLength.length > 4 || ImgLength.length + this.upImgCount > 5 ){
                    alert('亲，图片超出上限了！')
                    return
                }
                for(let i=0;i<ImgLength.length;i++){
                    let imgUrl = URL.createObjectURL(ImgLength[i])
                    this.upImgPreview.push({imgUrl:imgUrl,imgName:ImgLength[i].name}) // //添加上传图片 到数组 预览
                    this.ForumObj.upImg.push(ImgLength[i]) //添加上传图片 到数组 等待上传
                    this.upImgCount++                   //添加上传图片，计数 不大于4
                }
            },
            // 点击预览图片删除上传以及预览
            deletePreVeiwImg(src,name){
                if(confirm('你确定要取消上传这张图片吗？')){
                    for(let i=0;i<this.upImgCount;i++){
                        // 删除预览中的图片
                        if(this.upImgPreview[i].imgUrl === src){
                            this.upImgPreview.splice(i,1)
                            this.upImgCount--
                            
                        }
                        // 删除真正等待上传的图片文件
                        if(this.ForumObj.upImg[i].name === name){
                            this.ForumObj.upImg.splice(i,1)
                            break
                        }
                    }
                }
            },
            // 发布
            subMit(){
                if(this.ForumObj.ForumTitle == ''){
                    alert('标题不能为空')
                    return
                }
                if(this.ForumObj.ForumContent == ''){
                    alert('内容不能为空')
                    return
                }
                if(this.ForumObj.upImg.length > 0){
                    let file
                    for(let item of this.ForumObj.upImg){
                        file = this.Bmob.File(nanoid()+item.name,item)
                    }
                    file.save().then(res => {
                        // 添加数据
                        this.addData(this.ForumObj.UserName,this.ForumObj.ForumTitle,this.ForumObj.ForumContent,res)
                    })
                }else{
                    console.log('没文件');
                    this.addData(this.ForumObj.UserName,this.ForumObj.ForumTitle,this.ForumObj.ForumContent,[])
                }
                
            },
            /**
             * 数据库新增一条数据
             * @param  {...any} args 用户名称，论坛标题，论坛内容,上传的图片名，上传的图片URL
             */
            addData(...args){
                const query = this.Bmob.Query('FroumList')
                query.set('UserName',args[0])
                query.set('ForumTitle',args[1])
                query.set('ForumContent',args[2])
                query.set('SeeView',0)
                query.set('thumbs',0)
                query.set('CriticismCount',0)
                query.set('file',args[3])
                query.save().then(res=>{
                    alert('发布成功!')
                    console.log(res);
                    this.$router.push({
                       path:'/'
                    }),
                    this.$bus.$emit('changeReplay',false)
                }).catch(err=>{
                    alert('发布失败！')
                    console.log(err);
                })
            }
        },
    }

</script>

<style lang="css" scoped>
    /* 浏览全部论坛模块 */
    .ForumItem{
        background-color:#fefefe;
        width: 1200px;
        padding: 20px;
        box-shadow: 0px 0px 10px rgba(0,0,0,.2);
        position: relative;
        overflow: hidden;
        height: auto;
        padding: 20px 100px;
    }

    /* 背景大球 */
    .backgroundp-qiu{
        width: 160px;
        height: 160px;
        background-color: #b8e1e5;
        position: absolute;
        border-radius: 50%;
        right: -60px;
        bottom: -60px;
    }
    /* 背景小球 */
    .backgroundp-xiaoqiu{
        width: 60px;
        height: 60px;
        background-color: #b8e1e5;
        position: absolute;
        border-radius: 50%;
        right: -30px;
        bottom:110px;
    }
    /* <!-- 论坛的主体内容区的底部 --> */
    .ForumBodyBottom{
        width: 100%;
        height: 15px;
        background-color: #288384;
        border-bottom-left-radius: 12px;
        border-bottom-right-radius: 12px;

    }

    /* 发布内容样式 */
    /* 表格 */
    table td{
        height: 90px;
    }
    #lable-title{
        font-size: 30px;
        color: #000;
        /* line-height: 40px; */
        display: inline-block;
        font-weight: 500;
        /* margin-top: 5px; */
    }
    #title-input{
        width: 900px;
        border-radius: 16px;
        height: 40px;
        border: 3px solid #a4dadf;
        padding-left: 5px;
        margin-top: 7px;
    }

    #lable-content{
        display: inline-block;
        font-size: 30px;
        color: #000;
    }
    #content-input{
        width: 900px;
        height: 120px;
        border: 3px solid #a4dadf;
        padding: 5px;
        border-radius: 16px;
    }

    #lable-fileImg{
        font-size: 30px;
        color: #000;
        display:inline-block;
        margin-top: 30px;
        margin-bottom: 42px;
        
    }
    #fileImg-input{
        display: block;
        width: 160px;
        height: 160px;
        border: 3px solid #afdadf;
        border-radius: 16px;
        padding: 5px;
        /* background-image: url('./img/添加图片.png'); */
        background-repeat: no-repeat;
        background-position: 50% 50%;
        position: relative;
    }
    #fileImg-input::before{
        content: '';
        display: block;
        position: absolute;
        width: 160px;
        height: 160px;
        background-color: #fff;
        background-image: url('../static/img/addImg.png');
        background-repeat: no-repeat;
        background-position: 40% 40%;
    }
    /* 发布 和 放弃 按钮 */
    #issue{
        display: block;
        width: 720px;
        height: 80px;
        text-align: center;
        line-height: 80px;
        background-color: #288384;
        border: 3px solid #0a6baf;
        border-radius: 16px;
        font-size: 36px;
        color: #fff;
        margin: 0 auto;
        margin-top: 110px;
        cursor: pointer;
    }

    #abandon{
        display: block;
        width: 720px;
        height: 80px;
        text-align: center;
        line-height: 80px;
        border: 3px solid #0a6baf;
        border-radius: 16px;
        font-size: 36px;
        color: #288384;
        margin: 0 auto;
        margin-top: 30px;
        margin-bottom: 100px;
    }
    .yulan{
        display: block;
        width: 160px;
        height: 160px;
        margin-right: 10px;
        cursor: pointer;
    }
</style>