<template>
  <!-- 论坛的主体内容区 -->
  <div class="forumBody w">
        <!-- 导航区 -->
        <div class="bodyNav">
            <router-link to="/" class="routerLink LookForum" exact-active-class="active">
                <img src="../static/img/LookForum.png" ><span>看论坛</span>
            </router-link>
            <router-link to="/addFroum" class="routerLink WriteForum" exact-active-class="active">
                <img src="../static/img/WriteForum.png" ><span>写论坛</span>
            </router-link>
            <router-link to="/" class="repl" exact-active-class="reactive">
                <p @click="replay" class="replay">返回</p>
            </router-link>
        </div>

        <router-view></router-view>

        <!-- 论坛的主体内容区的底部 -->
        <div class="ForumBodyBottom">

        </div>
    </div>
</template>

<script>
    export default {
        name:'BodyForum',
        data(){
            return {
                isSee:false
            }
        },
        methods:{
            goIndex(){
                this.$router.push({
                    path:'/'
                })
            },
            goAddForum(){
                this.$router.push({
                    path:'/addFroum'
                })
            },
            // 返回到首页
            replay(){
                this.isSee = false
                this.$router.push({
                    path:'/'
                })
            },

            //接受信息，更改导航栏的选择状态
            changeReplay(t){
                this.isSee = t
            }

        },
        mounted(){
            //挂载全局总线
            this.$bus.$on('changeReplay',this.changeReplay)

        }
    }
</script>

