<template>
    <!-- 浏览全部论坛模块 -->
    <div class="ForumItem">
        <!-- 名称，头像和发布时间区 -->
        <div class="UserClass">
            <div class="UserHead">
                <img src="../static/image/tx1.jpg" >
            </div>
            <!-- 名称和发布时间和标签 -->
            <div class="UserName-date">
                <h3>{{Forum.UserName}}</h3>
                <p>{{Forum.createdAt}} &nbsp; #{{Forum.ForumTitle}}</p>
            </div>
            <!-- 每个模块中的举报 -->
            <div class="report">
                <button @click="deleteForum(Forum.objectId)" v-if="HomeUserName === Forum.UserName" class="report-delete">删除</button>
                <div @click="jubao" class="report-img-h5">
                    <img src="../static/img/Report.png">
                    <h5>举报</h5>
                </div>
            </div>
        </div>
        <!-- 线 -->
        <span class="line"></span>
        <!-- 内容显示区 -->
        <div class="content-show-Aree">
            <p class="content-text">{{Forum.ForumContent}}</p>
            <div v-if="Forum.file.length > 0" class="content-img">
                <img v-for="(f,index) in Forum.file" :key="index" :src="f.url" alt="图片已被管理员删除" onerror="this.classList.add('error')">
            </div>
        </div>

        <!-- 点赞,评论，浏览 -->
        <div class="operator">
            <div ref="thumbs" @click.once="activeThumbs(Forum.objectId,Forum.thumbs)" class="like">
                <img src="../static/img/Thumbs.png">
                <p v-text="Forum.thumbs > 10000 ? (Math.floor(Forum.thumbs / 10000) + '万+') : Forum.thumbs"></p>
            </div>
            <div class="comment">
                <img src="../static/img/Criticism.png">
                <p v-text="Forum.CriticismCount > 999 ? '999+':Forum.CriticismCount"></p>
            </div>
            <div class="browse">
                <img src="../static/img/View.png">
                <p v-text="Forum.SeeView > 10000 ? (Math.floor(Forum.SeeView / 10000) + '万+') : Forum.SeeView"></p>
            </div>
        </div>

        <!-- 写评论的地方 -->
        <div class="write-criticism">
            <input ref="fabu" v-model="Critis" type="text" id="writeCriticism-input">
            <div class="onSubit">
                <img src="../static/img/Face.png">
                <button class="onFb" @click="onFb(Forum.objectId)">发表</button>
            </div>
        </div>

        <!-- 查看评论的地方 -->
        <div class="look-criticism">
            <!-- 头像 名称 内容 -->
            <div v-show="replyUser.length > 0" v-for="reuser in replyUser" :key="reuser.objectId" class="tx-name-content">
                <div>
                <!-- 评论的人 -->
                <div  class="tx-name-date">
                    <img src="../static/image/tx1.jpg">
                    <div class="date-content">
                    <!-- 名称 -->
                        <p class="crit-name">{{reuser.replayUserName}}</p>
                        <p class="crit-date">{{reuser.createdAt}} 来自{{reuser.replyLocation}}</p>
                    </div>
                    <div class="right-dianzan">
                        <img src="../static/img/Thumbs.png">
                        <span @click="reuserThumb" v-text="reuser.replyThumbs > 10000 ? (Math.floor(reuser.replyThumbs / 10000) + '万+') : reuser.replyThumbs"></span>
                        <img @click="onReplay(reuser.objectId,reuser.replayUserName)" class="img2" src="../static/img/Criticism.png" >
                    </div>
                </div>
                <div class="content">
                    <p class="crit-content">{{reuser.replyContent}}</p>
                    <button @click="deleteReply(reuser.objectId)" v-if="(HomeUserName === reuser.replayUserName) || (HomeUserName === Forum.UserName)" class="crit-delete">删除</button>
                </div>
                </div>
                <!-- 回复评论的 -->
                <div class="reply-crit">
                    <!-- <template v-if="remark.replyUserID === reuser.objectId"> -->
                    <div v-for="remark in isReplay(reuser.objectId)" :key="remark.objectId" >
                        <div class="reply-crit-tx-name-date">
                            <img src="../static/image/tx1.jpg">
                            <div class="reply-crit-date-content">
                            <!-- 名称 -->
                                <p class="reply-crit-crit-name">{{remark.remarkUsername}}</p>
                                <p class="reply-crit-crit-date">{{remark.createdAt}} 来自{{remark.remarkLocation}}</p>
                            </div>
                            <div @click="replyThumbs" class="reply-crit-right-dianzan">
                                <img src="../static/img/Thumbs.png">
                                <span v-text="remark.remarkThumbs > 10000 ? (Math.floor(remark.remarkThumbs / 10000) + '万+') : remark.remarkThumbs"></span>
                                <!-- <img  class="reply-crit-img2" src="../static/img/Criticism.png" > -->
                            </div>
                        </div>
                        <div class="reply-content">
                            <p class="reply-crit-content">{{remark.remarkContent}}</p>
                            <button @click="deleteReplyRe(remark.objectId)"  v-if="(HomeUserName === remark.remarkUsername) || (HomeUserName === Forum.UserName)" class="reply-crit-delete">删除</button>
                        </div>
                    </div>
                    <!-- </template> -->
                    
                </div>
            </div>
        </div>

        <!-- 背景大球 -->
        <div class="backgroundp-qiu"></div>
        <!-- 背景小球 -->
        <div class="backgroundp-xiaoqiu"></div>
    </div>
</template>

<script>
    export default {
        name:'SeeFroum',
        data(){
            return {
                // 论坛内容
                Forum:{
                    file:[],
                    // CriticismCount:0
                },
                // 评论的全部内容
                replyUser:[],
                // 回复评论的全部内容
                replyRe:[],
                // 获取本地名称
                HomeUserName:localStorage.getItem('UserName'),
                // 带发表的评论
                Critis:'',
                // 评论的ID
                replayId:'',
            }
        },
        computed:{
            // 返回一个函数，函数里面返回符合条件的数组
            // 返回一个新的数组，新数组是通过 被回复人的ID === 评论人的ID
            isReplay(){
                return function(id){
                    return this.replyRe.filter(item => {
                        return item.replyUserID === id
                    })
                }
            }
        },
        methods:{
            // 按ID查询FroumList中的一条数据
            queryFroumListbyItem(id){
                // 发送请求获取数据信息
                const query = this.Bmob.Query('FroumList');
                query.get(id).then(res => {
                    this.Forum = res
                }).catch(err => {
                    console.log(err)
                    alert('服务器出错了！'+ err)
                })
            },
            // 查询ID符合条件的所有数据
            queryReplyUser(id){
                const query = this.Bmob.Query("replyUser");
                query.order("-replyThumbs","-createdAt");
                query.equalTo("FroumItemID","==", id);
                query.find().then(res => {
                    this.replyUser = res
                });
            },
            /**
             * 查询符合条件的数据
             * @param {*属于哪个论坛的ID} Fid 
             */
            queryReplayRe(Fid){
                const query = this.Bmob.Query("replayRe");
                query.equalTo("ForumItemID","==", Fid);
                // query.equalTo("replyUserID","==", Rid);
                query.find().then(res => {
                    this.replyRe = res
                });
            },
            //发表普通评论
            /**
             * 发布评论
             * @param {发布评论的论坛ID} Fid 
             * @param {发布的内容} content 
             */
            addComment(Fid,content){
                const query = this.Bmob.Query('replyUser');
                query.set("FroumItemID",Fid)
                query.set("replyContent",content)
                query.set("replayUserName",this.HomeUserName)
                query.set("replyLocation",'安徽')
                query.set("replyThumbs",0)
                query.save().then(res => {
                    console.log(res)
                    alert('发布成功!')
                    this.$router.go(0)
                }).catch(err => {
                    console.log(err)
                })

                const query1 = this.Bmob.Query('FroumList')
                query1.get(Fid).then(res => {
                console.log(res)
                    res.set('CriticismCount',this.Forum.CriticismCount+1)
                    res.save()
                }).catch(err => {
                    console.log(err)
                })
            },
            /**
             * 回复评论
             * @param {发布评论的论坛ID} Fid 
             * @param {被回复的评论ID} Rid 
             * @param {回复的内容} content 
             */
            replyComment(Fid,Rid,content){
                const query = this.Bmob.Query('replayRe');
                query.set("ForumItemID",Fid)
                query.set("replyUserID",Rid)
                query.set("remarkContent",content)
                query.set("remarkUsername",this.HomeUserName)
                query.set("remarkLocation",'安徽')
                query.set("remarkThumbs",0)
                query.save().then(res => {
                    console.log(res)
                    alert('回复成功!')
                    this.$router.go(0)
                }).catch(err => {
                    console.log(err)
                })

                const query1 = this.Bmob.Query('FroumList')
                query1.get(Fid).then(res => {
                console.log(res)
                    res.set('CriticismCount',this.Forum.CriticismCount+1)
                    res.save()
                }).catch(err => {
                    console.log(err)
                })
            },
            // 发表评论
            onFb(Fid){
                // 发表的评论不能为空
                if(this.Critis!=''){
                    let strArr = this.Critis.split(':')
                    let reg = /^@.*@$/
                    if(reg.test(strArr[0])){
                        //回复发表
                        console.log(strArr);
                        if(strArr[1]!==undefined){
                            let newstr = strArr[1].trim()
                            if(newstr !==''){
                                if(this.replayId != '' && this.ByreName !=''){
                                    this.replyComment(Fid,this.replayId,newstr)
                                }else{
                                    alert('不正规的发表')
                                }
                            }else{
                                alert('回复的信息不能为空!')
                            }
                            
                        }else{
                            this.addComment(Fid,strArr[0])
                        }
                    }else{
                        //普通发表
                        this.addComment(Fid,strArr[0])
                    }
                }else{
                    alert('评论不能为空!')
                }
                
            },
            //带回复状态
            onReplay(Rid,ByName){
                this.replayId = Rid
                this.ByreName = ByName
                this.Critis = `@${ByName}@:`
                this.$refs.fabu.focus()
                window.scrollTo(0,100)
            },
            // 删除论坛
            deleteForum(id){
                // 先删除论坛
                const query = this.Bmob.Query('FroumList');
                query.destroy(id).then(res => {
                    console.log(res)
                    alert('删除成功！')
                    this.$router.push('/')
                }).catch(err => {
                    console.log(err)
                })
                // 在删除论坛中的评论
                const query1 =this.Bmob.Query('replyUser');
                // 单词最多删除50条
                query1.limit(50)
                query1.equalTo("FroumItemID", "==", id);
                query1.find().then(todos => {
                    todos.destroyAll().then(res => {
                        // 成功批量修改
                        console.log(res,'ok')
                    }).catch(err => {
                        console.log(err)
                    });
                })
                //删除论坛中的评论的回复
                const query2 =this.Bmob.Query('replayRe');
                // 单词最多删除50条
                query2.limit(50)
                query2.equalTo("ForumItemID", "==", id);
                query2.find().then(todos => {
                    todos.destroyAll().then(res => {
                        // 成功批量修改
                        console.log(res,'ok')
                    }).catch(err => {
                        console.log(err)
                    });
                })
            },
            // 删除评论
            deleteReply(id){
                if(confirm('确定要删除吗？')){
                    // 在删除论坛中的评论
                    const query = this.Bmob.Query('replyUser');
                    query.destroy(id).then(res => {
                        console.log(res)
                        alert('删除成功！')
                        this.$router.go(0)
                    }).catch(err => {
                        console.log(err)
                    })
                    //删除论坛中的评论的回复
                    const query2 =this.Bmob.Query('replayRe');
                    // 单词最多删除50条
                    query2.limit(50)
                    query2.equalTo("replyUserID", "==", id);
                    query2.find().then(todos => {
                        todos.destroyAll().then(res => {
                            // 成功批量修改
                            console.log(res,'ok')
                            this.$router.go(0)
                        }).catch(err => {
                            console.log(err)
                        });
                    })
                }
            },
            // 删除回复
            deleteReplyRe(id){
                if(confirm('确定要删除吗？')){
                    // 在删除论坛中的评论
                    const query = this.Bmob.Query('replayRe');
                    query.destroy(id).then(res => {
                        console.log(res)
                        alert('删除成功！')
                        this.$router.go(0)
                    }).catch(err => {
                        console.log(err)
                    })
                }
            },
            //点赞论坛
            activeThumbs(id,thumbsCount){
                console.log(id,thumbsCount);
                let thumbsd = thumbsCount + 1
                const query = this.Bmob.Query('FroumList');
                query.get(id).then(res => {
                    res.set('thumbs',thumbsd)
                    res.save()
                    this.Forum.thumbs++
                    this.$refs.thumbs.className = this.$refs.thumbs.className + ' like-active'
                }).catch(err => {
                    console.log(err)
                })
            },
            // 评论的点赞
            reuserThumb(){
                alert('功能暂未开放!')
            },
            //回复的评论
            replyThumbs(){
                alert('功能暂未开放!')
            },
            // 举报
            jubao(){
                alert('功能暂未开放!')
            }
        },
        mounted(){
            // 接受传过来的ID
            let id = this.$route.params.id
            // 按ID查询数据
            this.queryFroumListbyItem(id)
            // 按ID查询所有符合条件的评论
            this.queryReplyUser(id)
            // 按ID查询所有符合条件的回复
            this.queryReplayRe(id)

        }
    }
</script>

