// 引入路由
import Router from 'vue-router'

// 引入组件
import SeeFroum from '../pages/SeeFroumList.vue'
import WriteForum from '../pages/WriteForum.vue'
import LookFroum from '../pages/SeeFroum.vue'

// 创建路由对象
export default new Router({
    mode:'history',
    routes:[
        {
            name:'index',
            path:'/',
            component:SeeFroum
        },
        {
            path:'/addFroum',
            component:WriteForum,

        },
        {
            name:'lookFroum',
            path:'/lookFroum/:id',
            component:LookFroum
        }
    ]
})