<template>
    <div>
        <!-- 浏览全部论坛模块 -->
        <div @click="access(forum.objectId,forum.SeeView)" class="ForumItem" v-for="forum in ForumArr" :key="forum.objectId">
                <!-- 名称，头像和发布时间区 -->
                <div class="UserClass">
                    <div class="UserHead">
                        <img src="../static/image/tx1.jpg" >
                    </div>
                    <!-- 名称和发布时间和标签 -->
                    <div class="UserName-date">
                        <h3>{{forum.UserName}}</h3>
                        <p>{{forum.createdAt}} &nbsp; #{{forum.ForumTitle}}</p>
                    </div>
                    <!-- 每个模块中的举报 -->
                    <div class="report">
                        <img src="../static/img/Report.png">
                        <h5>举报</h5>
                    </div>
                </div>
                <!-- 线 -->
                <span class="line"></span>
                <!-- 内容显示区 -->
                <div class="content-show-Aree">
                    <p class="content">{{forum.ForumContent}}</p>
                    <div class="content-img" v-show="forum.file.length > 0">
                        <img crossorigin="anonymous" v-for="(item,index) in forum.file" :key="index" :src="item.url" alt="图片已被管理员删除" onerror="this.classList.add('error')">
                    </div>
                </div>

                <!-- 点赞,评论，浏览 -->
                <div class="operator">
                    <div class="like">
                        <img src="../static/img/Thumbs.png">
                        <p v-text="forum.thumbs > 10000 ? (Math.floor(forum.thumbs / 10000) + '万+') : forum.thumbs"></p>
                    </div>
                    <div class="comment">
                        <img src="../static/img/Criticism.png">
                        <p v-text="forum.CriticismCount > 999 ? '999+':forum.CriticismCount"></p>
                    </div>
                    <div class="browse">
                        <img src="../static/img/View.png">
                        <p v-text="forum.SeeView > 10000 ? (Math.floor(forum.SeeView / 10000) + '万+') : forum.SeeView"></p>
                    </div>
                </div>

                <!-- 背景大球 -->
                <div class="backgroundp-qiu"></div>
                <!-- 背景小球 -->
                <div class="backgroundp-xiaoqiu"></div>
        </div>
    </div>
</template>

<script>
    export default {
        name:'SeeFroumList',
        data(){
            return {
                ForumArr:[],
                // 记录滚动条的高度
                // scrollYD:0,
                timer:'',
            }
        },
        methods:{
            access(id,SeeView){
                const query1 = this.Bmob.Query('FroumList')
                query1.get(id).then(res => {
                console.log(res)
                    res.set('SeeView',SeeView+1)
                    res.save()
                    localStorage.setItem('scrollYD',window.scrollY)
                    //跳转
                    this.$router.push({
                    name:'lookFroum',
                    params:{
                        id:id
                    }
                })
                }).catch(err => {
                    console.log(err)
                })
            },
            handleScroll(){

                clearTimeout(this.timer)
                this.timer = setTimeout(()=>{
                    localStorage.setItem('scrollYD',window.scrollY)
                },500)
            }
        },
        mounted(){
            //请求论坛数据
            const query = this.Bmob.Query('FroumList')
            query.order("-createdAt","-thumbs");
            query.find().then(res => {
                this.ForumArr = res
            })
            window.addEventListener('scroll',this.handleScroll)
        },
        updated(){
            window.scrollTo(0,localStorage.getItem('scrollYD'))
        },
        beforeDestroy(){
            window.removeEventListener('scroll',this.handleScroll)
            clearTimeout(this.timer)
        }

    }
</script>

<style scoped>
    /* 浏览全部论坛模块 */
    .ForumItem{
        background-color: #fefefe;
        margin-top: 20px;
        width: 1200px;
        padding: 20px;
        box-shadow: 0px 0px 10px rgba(0,0,0,.2);
        margin-bottom: 20px;
        position: relative;
        overflow: hidden;
        height: auto;
    }

    /* 论坛每个模块中的用户样式 */
    .UserClass{
        display: flex;
        position: relative;
    }
    /* 用户头像 */
    .UserHead{
        width: 60px;
        height: 60px;
        border-radius: 50%;
    }
    .UserHead img{
        width: 100%;
        height: 100%;
        border-radius: 50%;
    }

    /* <!-- 名称和发布时间和标签 --> */
    .UserName-date{
        height: 60px;
        margin-left: 10px;
    }
    .UserName-date h3{
        margin-top: 3px;
        font-size: 24px;
        color: #333;
    }
    .UserName-date p{
        
        font-size: 18px;
        color: #666;
    }

    /* <!-- 每个模块中的举报 --> */
    .report{
        margin-top: 5px;
        width: 37px;
        height: 34px;
        position: absolute;
        right: 10px;
    }
    .report img{
        width: 100%;
        height: 100%;
    }
    .report h5{
        font-size: 18px;
        margin-top: -8px;
    }

    /* 线 */
    .line{
        display: block;
        border-bottom: 1px solid #ccc;
        width: calc(100% - 50px);
        margin-top: 16px;
        margin-left: 70px;
    }

    /* 内容显示器 */
    .content-show-Aree{
        margin-top: 16px;
        margin-left: 70px;
        width: 1020px;
    }

    /* 内容文字 */
    .content{
        /* display: block; */
        font-size: 24px;
        color: #333;
        line-height: 24pxs;
    }
    /* 图片内容 */
    .content-img{
        display: flex;
        margin: 20px 0;
    }
    .content-img img{
        width: 140px;
        height: 140px;
        margin-right: 20px;
    }

    /* 图片显示错误触发样式 */ 
    img.error {
        display: inline-block;
        transform: scale(1);
        content: '';
        color: transparent;
    }
    img.error::before {
        content: '';
        position: absolute;
        left: 0; top: 0;
        width: 100%; height: 100%;
        background: #c6c6c6 url('../static/img/Report.png') no-repeat center / 50% 50%;
    }
    img.error::after {
        content: attr(alt);
        position: absolute;
        left: 0; bottom: 0;
        width: 100%;
        line-height: 2;
        background-color: rgba(0,0,0,.5);
        color: white;
        font-size: 12px;
        text-align: center;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    /* <!-- 点赞,评论，浏览 --> */
    .operator{
        margin: 0 auto;
        width: 800px;
        margin-top: 60px;
        display: flex;
        justify-content: space-around;
        margin-bottom: 20px;
    }

    /* 点赞 */
    .like{
        display: flex;
        cursor: pointer;
    }
    .like img{
        width: 27px;
        height: 25px;
    }
    .like p{
        font-size: 20px;
        color: #333;
    }

    /* 评论 */
    .comment{
        display: flex;
        cursor: pointer;
    }
    .comment img{
        width: 27px;
        height: 25px;
        margin-top: 3px;
        margin-right: 5px;
    }
    .comment p{
        font-size: 20px;
        color: #333;
    }

    /* 浏览 */
    .browse{
        display: flex;
    }
    .browse img{
        width: 27px;
        height: 25px;
        margin-top: 1px;
    }
    .browse p{
        font-size: 20px;
        color: #333;
    }
    /* 背景大球 */
    .backgroundp-qiu{
        width: 160px;
        height: 160px;
        background-color: #b8e1e5;
        position: absolute;
        border-radius: 50%;
        right: -60px;
        bottom: -60px;
    }
    /* 背景小球 */
    .backgroundp-xiaoqiu{
        width: 60px;
        height: 60px;
        background-color: #b8e1e5;
        position: absolute;
        border-radius: 50%;
        right: -30px;
        bottom:110px;
    }
</style>